<template>
  <div class="er-frame-container">
    <div class="er-block-container er-login-container">
      <h1 class="er-login-title"></h1>
      <div class="er-login-form">
        <el-form ref="form" :model="form" :rules="rules" label-width="80px">
          <el-form-item label="" prop="username">
            <el-input v-model="form.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="form.password" type="password"></el-input>
          </el-form-item>
<!--
          <el-form-item label="" prop="role">
            <el-select v-model="form.role" placeholder="">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="10"></el-option>
              <el-option label="" value="100"></el-option>
              <el-option label="" value="1000"></el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item>
            <el-button type="primary" @click="onSubmit('form')" size="large" class="er-login-button"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>

import { USER_LOGIN,UPDATE_ROLE } from '@/store/mutation_types'
export default {
  data() {
    return {
      gamesData: [],
      form: {
        username: '',
        password: '',
        // role: ''
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 50, message: ' 6  50 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 12, message: ' 6  12 ', trigger: 'blur' }
        ]
        // ,
        // role: [
        //   { required: true, message: '', trigger: 'change' }
        // ]
      }
    }
  },
  methods: {
    doLogin(userInfo) {
      const user = {...userInfo}
      return this.$store.dispatch(USER_LOGIN,user)
    },
    onSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        let loading = this.$loading({
          target: "#frameWrapper"
        })
        if (valid) {
          this.doLogin(this.form).then(result =>{
            loading.close()   //loading
            this.addSuccess()
            console.log(result);
            this.$router.replace({path: '/'})
          }).catch(result=>{
            loading.close()
            this.addError()
            return false
          })
        } else {
          return false
        }
      })
    },
    addSuccess() { this.$message({ type: 'success', message: '!' }) },
    addError(errMsg) {
      let dfMsg = ''
      if (!errMsg) { errMsg = dfMsg }
      this.$message({
        type: 'error',
        message: errMsg
      })
    },
    resetForm(formName) { this.$refs[formName].resetFields() },
    points2Num() {
      let num = Number(this.form.points);
      if (this.form.points == num) {
        this.form.points = num;
      } else {
        return
      }
    }
  }
}
</script>



