<template>
  <div class="er-frame-container">
    <div class="er-block-container er-frame-title">
      <h1>{{ frameTitle }}</h1>
    </div>
    <div class="er-block-container">
      <el-form ref="form" :model="form" :rules="rules" label-width="120px">
        <el-form-item label="" prop="email">
          <el-input v-model="form.email" r></el-input>
        </el-form-item>
        <el-form-item label="" prop="child">
          <div> {{form.username}} </div>
        </el-form-item>
        <el-form-item label="" prop="site">
          <el-input v-model="form.site"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ratio">
          <el-input v-model="form.ratio"></el-input>
        </el-form-item>
        <el-form-item label="" prop="nick">
          <el-input v-model="form.nick"></el-input>
        </el-form-item>
        <el-form-item label="" prop="msn">
          <div>{{form.assets.msn}}</div>
        </el-form-item>
        <el-form-item label="" prop="points">
          <div> {{points}} </div>
        </el-form-item>
        <el-form-item label="" prop="gameList" class="er-long-select">
          <el-select v-model="form.gameList" placeholder="" multiple>
            <el-option v-for="item in gamesData" :key="item.sk" :label="item.name" :value="item.type"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="enabled">
          <el-select v-model="form.enabled" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="remark">
          <el-input type="textarea" v-model="form.remark"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit('form')"></el-button>
          <el-button @click="resetForm('form')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import {
  awsFetch,
  zeusFetch
} from '@/libs/awsLib'
import {
  MERCHANT_ROLE
} from '@/config/aws-config'
import _ from 'lodash'
export default {
  created() {
    let loading = this.$loading({
      target: "#frameWrapper"
    });
    // load all data from show api
    // but only those in the data can be update
    const getItem = {
      url: '/merchants/'+ this.$route.params.id,
      method: 'get'
    }
    zeusFetch(getItem).then(result=>{
      loading.close();    //loading
      const [err, ret] = result
      this.form = {...ret.data.payload.Item}
      const getPoints = {
        url: '/points/' + this.form.username + '/' + MERCHANT_ROLE,
        method: 'get'
      }
      zeusFetch(getPoints).then(result=>{
        loading.close();    //loading
        const [err, ret] = result
        this.points = ret.data.payload.points// _.reduce(_.map(ret.data.payload.Items,item=>{ return item['assets']['points'] }),(result,n)=>{return result + n})
      }).catch(err=>{
        loading.close();    //loading
      })

    }).catch(err=>{
      loading.close();    //loading
    })

    const gameItem = {
      url: '/games/',
      method: 'get'
    }
    zeusFetch(gameItem).then(result=>{
      loading.close();    //loading
      const [err, ret] = result
      this.gamesData = [...ret.data.payload.Items]
    }).catch(err=>{
      loading.close();    //loading
    })


  },
  data() {
    return {
      frameTitle: '',
      gamesData: [],
      points:'',
      form: {
        email: '',
        ratio:'',
        username: '',
        assets:{
          msn:'',
          ratio:''
        },
        gameList: [],
        enabled: '',
        remark: '',
        nick:''
      },
      rules: {
        email: [
          { type: 'email', required: true, message: '', trigger: 'blur' }
        ],
        // username: [
        //   { required: true, message: '', trigger: 'blur' }
        // ],
        // msn: [
        //   { required: true, message: '', trigger: 'blur' }
        // ],
        // points: [
        //   { type: 'number', required: true, message: '', trigger: 'blur' }
        // ],
        gameList: [
          { type: 'array', required: true, message: '', trigger: 'change' }
        ],
        enabled: [
          { required: true, message: '', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    onSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.postData();
        } else {
          return false;
        }
      });
    },
    postData() {
      let loading = this.$loading({
        target: "#frameWrapper"
      });
      const request = {
        url: '/merchants/'+this.$route.params.id,
        method: 'put',
        data:this.form
      }
      zeusFetch(request).then(result=>{
        loading.close();    //loading
        this.addSuccess();  //
        this.$router.replace('/merchants')
      }).catch(err=>{
        loading.close();    //loading
        this.addError()
      })
    },
    addSuccess() {
      this.$message({
        type: 'success',
        message: '!'
      });
    },
    addError(errMsg) {
      let dfMsg = '';
      if(!errMsg){
        errMsg = dfMsg;
      }
      this.$message({
        type: 'error',
        message: errMsg
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    points2Num() {
      let num = Number(this.form.points);
      if (this.form.points == num) {
        this.form.points = num;
      } else {
        return
      }
    }
  }

}
</script>



