<template>
<div class="er-nav-menu">
  <el-menu v-if='this.$store.state.showRouter' :default-active="defaultActive" :unique-opened="uniqueOpened" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" theme="dark" :router="router">
    <el-menu-item index="/"></el-menu-item>
    <el-submenu index="2" >
      <template slot="title"></template>
      <el-menu-item v-if="this.$store.state.role == '1' || this.$store.state.role == '0'" index="/merchants/add"></el-menu-item>
      <el-menu-item v-if="this.$store.state.role == '100' || this.$store.state.role == '10' || this.$store.state.role == '1' || this.$store.state.role == '0'" index="/merchants"></el-menu-item>
    </el-submenu>
    <!-- <el-submenu index="3"  v-if="this.$store.state.role == '1000' || this.$store.state.role == '1' || this.$store.state.role == '0'">
      <template slot="title"></template>
      <el-menu-item index="/agents/add"></el-menu-item>
      <el-menu-item index="/agents"></el-menu-item>
    </el-submenu> -->
    <el-submenu index="4">
      <template slot="title"></template>
      <el-menu-item v-if="this.$store.state.role == '10' || this.$store.state.role == '1' || this.$store.state.role == '0'" index="/managers/add"></el-menu-item>
      <el-menu-item v-if="this.$store.state.role == '10' || this.$store.state.role == '1' || this.$store.state.role == '0'" index="/managers"></el-menu-item>
    </el-submenu>
    <el-submenu index="5" v-if="this.$store.state.role=='1' || this.$store.state.role == '0' ">
      <template slot="title"></template>
      <el-menu-item index="/games/add"></el-menu-item>
      <el-menu-item index="/games"></el-menu-item>
    </el-submenu>
    <!-- <el-submenu v-if="this.$store.state.role == '1000' ||  this.$store.state.role == '1' || this.$store.state.role == '0'" index="6">
      <template slot="title"></template>
      <el-menu-item index="/players/add"></el-menu-item>
      <el-menu-item index="/players"></el-menu-item>
    </el-submenu> -->
    <el-menu-item v-if="this.$store.state.role == '0' || this.$store.state.role == '1' " index="/numbers"></el-menu-item>
    <el-submenu index="7" v-if="this.$store.state.role != '-1'">
      <template slot="title"></template>
      <el-menu-item index="/profile"></el-menu-item>
      <el-menu-item index="/changepassword"></el-menu-item>
      <el-menu-item index="/merchants/players"></el-menu-item>
      <el-menu-item index="/merchants/1/games"></el-menu-item>
      <el-menu-item index="/billing"></el-menu-item>
    </el-submenu>
    <el-menu-item  index="/playerregister"></el-menu-item>
  </el-menu>
</div>
</template>

<script>
import { RETRIVE_USER, SHOW_ROUTER } from '@/store/mutation_types'
export default {
  name: 'navMenu',
  props: ['defaultActive'],
  data() {
    return {
      uniqueOpened: true,
      router: true
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    }
  },
  mounted() {
    this.$store.dispatch(RETRIVE_USER).then((res) => {
      this.$store.dispatch(SHOW_ROUTER)
    }).catch((res) => {
      this.$store.dispatch(SHOW_ROUTER)
    })
  }
}
</script>



